.HobbyList {
  font-size: 1.2rem;
  font-weight: 400;
  text-align: left;
  padding-bottom: 1rem;
  color: #7c493d;
}

ul.HobbyList {
  list-style-type: square;
  width: 15rem;
  margin: 0 auto;
}

@media (min-width: 750px) {
  .HobbyList {
    font-size: 1.3rem;
  }

  ul.HobbyList {
    width: 10rem;
  }
}

@media (min-width: 900px) {
  ul.HobbyList {
    width: 15rem;
  }
}

@media (min-width: 1900px) {
  .HobbyList {
    font-size: 1.4rem;
  }
}
